'use client'

import { useEffect, useState } from 'react'
import { Button } from '@/components/ui/Button'
import { fetchCategories } from '@/lib/api'

interface Category {
    id: number
    name: string
    description: string | null
    materials: any[] // We'll type this properly later
}

export default function CategoriesPage() {
    const [categories, setCategories] = useState<Category[]>([])
    const [isLoading, setIsLoading] = useState(true)
    const [error, setError] = useState<string | null>(null)

    useEffect(() => {
        loadCategories()
    }, [])

    const loadCategories = async () => {
        try {
            const data = await fetchCategories()
            setCategories(data)
            setError(null)
        } catch (e) {
            setError('Failed to load categories')
        } finally {
            setIsLoading(false)
        }
    }

    const handleAddCategory = () => {
        alert('Add new category clicked')
    }

    return (
        <div>
            <div className="mb-6 flex justify-between items-center">
                <h1 className="text-2xl font-semibold text-gray-900">Categories</h1>
                <Button onClick={handleAddCategory}>
                    Add Category
                </Button>
            </div>

            <div className="bg-white shadow rounded-lg divide-y divide-gray-200">
                {isLoading ? (
                    <div className="p-6 text-center">Loading...</div>
                ) : error ? (
                    <div className="p-6 text-center text-red-500">{error}</div>
                ) : categories.length === 0 ? (
                    <div className="p-6 text-center text-gray-500">No categories found</div>
                ) : (
                    categories.map((category) => (
                        <div key={category.id} className="p-6 flex justify-between items-center">
                            <div>
                                <h3 className="text-lg font-medium text-gray-900">{category.name}</h3>
                                {category.description && (
                                    <p className="mt-1 text-sm text-gray-500">{category.description}</p>
                                )}
                                <p className="mt-1 text-sm text-gray-500">
                                    {category.materials.length} materials
                                </p>
                            </div>
                            <div className="flex gap-2">
                                <Button
                                    variant="outline"
                                    onClick={() => alert(`Edit category ${category.id}`)}
                                >
                                    Edit
                                </Button>
                                <Button
                                    variant="outline"
                                    onClick={() => alert(`Delete category ${category.id}`)}
                                    className="text-red-600 hover:text-red-700"
                                >
                                    Delete
                                </Button>
                            </div>
                        </div>
                    ))
                )}
            </div>
        </div>
    )
}
